import logo from './logo.svg';
import './App.css';
import React, { Component } from 'react'
import ResultsComponent from './ResultsComponent';
import KeysComponent from "./KeysComponent";

class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            result: ""
        }
    }
    
    //OnClick function
    onClick = button => {
        if(button === "="){
            this.calculate()
        }
        else if(button === "C"){
            this.reset()
        }
        else if(button === "CE"){
            this.backspace()
        }
        else {
            this.setState({result:this.state.result + button})
        }
    };

    //Calculate function
    calculate = () => { 
        //console.log(this.state.result);
        var checkRes = ''
        if(this.state.result.includes('--')){
            checkRes = this.state.result.replace('--','+')
        }
        else {
            checkRes = this.state.result
        }
        try {
            this.setState({
                result: (eval(checkRes) || "" ) + ""
                
            })
        } catch (e) {
            this.setState({result: "error"})
        }
    };
   
    //Reset function
    reset = () => {
        this.setState({result: "" })
    };

    //Backspace function
    backspace = () => {
        this.setState({result: this.state.result.slice(0, -1)})
    };

    render() {
        return (
            <div className="container">
             <br/> <h1>Calculator Application</h1><br/>
                <div className="calculator">   
                    <ResultsComponent result={this.state.result}/>
                    <KeysComponent onClick={this.onClick}/>
                </div>
            </div>
        );
    }
}


export default App;